// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Topic } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetTopic() {
  const [data, setData] = useState<Response<Topic[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.topic.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createTopic(topic: any) {
  const { data } = await sdk.course.offline.topic.create(topic);

  return data;
}

// ----------------------------------------------------------------------

export async function updateTopic(topic: any, id: string) {
  const { data } = await sdk.course.offline.topic.update(id, topic);

  return data;
}
