// component
import { CustomInfoBar } from 'src/components/custom-info-bar';
import Markdown from 'src/components/markdown';
// mui
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import TableCell from '@mui/material/TableCell';
import { QUESTION_ENUM_INDEX } from '../constant';

// ----------------------------------------------------------------------

export const TableHeader = [
  { id: 'content', label: 'Câu hỏi', flex: 1 },
  { id: 'type', label: 'Loại câu hỏi', flex: 1 },
  { id: 'createdAt', label: 'Ngày tạo', flex: 1, align: 'center' },
  { id: 'updatedAt', label: 'Ngày cập nhật', flex: 1, align: 'center' },
  { id: 'answer', label: 'Danh sách đáp án', flex: 1, align: 'center' },
  { id: 'action', label: 'Hành động', flex: 1, align: 'right' },
];

export const TableRows = (row: any) => {
  return (
    <>
      <TableCell
        sx={{
          width: 350,
        }}
      >
        <Markdown children={row?.content} />
      </TableCell>

      <TableCell>
        <CustomInfoBar
          primary={row?.type ? 'Câu hỏi trắc nghiệm' : 'Câu hỏi tự luận'}
          secondary={`Độ khó ${row?.level || 0} / 10`}
        />
      </TableCell>
    </>
  );
};

export const TableRowCollapse = (row: any) => {
  return (
    <Stack component={Paper} sx={{ m: 1.5 }}>
      {Array.from({ length: 4 })?.map((question: any, index: number) => (
        <Stack
          key={index}
          direction="row"
          alignItems="center"
          sx={{
            p: 1,
            '&:not(:last-of-type)': {
              borderBottom: (theme) => `solid 2px ${theme.palette.background.neutral}`,
            },
          }}
        >
          <CustomInfoBar
            primary={`Đáp án ${QUESTION_ENUM_INDEX[index]}`}
            secondary={<Markdown children={row?.[`answer0${index + 1}`]} />}
          />
        </Stack>
      ))}
    </Stack>
  );
};
