// react
import { useEffect, useMemo, useState } from 'react';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// @mui
import LoadingButton from '@mui/lab/LoadingButton';
import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import MenuItem from '@mui/material/MenuItem';
import Stack from '@mui/material/Stack';
// components
import Editor from 'src/components/editor';
import FormProvider, {
  BlockItem,
  RHFEditor,
  RHFSelect,
  RHFSlider,
  RHFTextField,
} from 'src/components/hook-form';
import Iconify from 'src/components/iconify';
import { useSnackbar } from 'src/components/snackbar';
// sdk
import { Subject } from '@iit/sdk/dist/types';
// api
import { createQuestion, updateQuestion } from 'src/api/controllers/question';
// context
import { useTableContext } from '../../table/context';
import { QUESTION_ENUM_INDEX, TEST_DIFFICULT } from '../constant';

// ----------------------------------------------------------------------

type Props = {
  open: boolean;
  onClose: VoidFunction;
  topics: any;
  subjects: any;
};

const QUESTION_TYPE = [
  {
    label: 'Câu hỏi trắc nghiệm',
    value: 'choice',
  },
  {
    label: 'Câu hỏi tự luận',
    value: 'essay',
  },
];

export default function QuestionForm({ open, onClose, topics, subjects }: Props) {
  const {
    _table_row_selected: currentValues,
    onSetTableRowSelected,
    onCreateNewRow,
    onUpdateRow,
  } = useTableContext();

  const isEdit = !!currentValues;

  const [answers, setAnswers] = useState<{ content: string }[]>([]);

  useEffect(() => {
    if (currentValues) {
      setAnswers(
        Array?.from({ length: 4 })?.map((answer: any, index: number) => ({
          content: currentValues?.[`answer0${index + 1}`],
        }))
      );
    }
  }, [currentValues]);

  const { enqueueSnackbar } = useSnackbar();

  const Schema = Yup.object().shape({
    content: Yup.mixed(),
    type: Yup.string().required('Bạn chưa chọn loại câu hỏi !'),
    topic: Yup.string().required('Bạn chưa chọn chủ đề !'),
    subject: Yup.string().required('Bạn chưa chọn môn học !'),
    level: Yup.number().required('Bạn chưa nhập độ khó câu hỏi !'),
    point: Yup.number().required('Bạn chưa nhập số điểm cho câu hỏi !'),
    lines: Yup.number().required('Bạn chưa nhập số hàng cho câu hỏi !'),
    answer01: Yup.mixed(),
    answer02: Yup.mixed(),
    answer03: Yup.mixed(),
    answer04: Yup.mixed(),
    answer05: Yup.mixed(),
    answer06: Yup.mixed(),
  });

  const defaultValues = useMemo(
    () => ({
      content: currentValues?.content || '',
      topic: currentValues?.topic?.id || '',
      subject: currentValues?.topic?.subject?.id || '',
      type: currentValues?.type || 'choice',
      level: currentValues?.level || 0,
      point: currentValues?.point || 0,
      lines: currentValues?.lines || 0,
      answer01: currentValues?.answer01 || '',
      answer02: currentValues?.answer02 || '',
      answer03: currentValues?.answer03 || '',
      answer04: currentValues?.answer04 || '',
      answer05: currentValues?.answer05 || '',
      answer06: currentValues?.answer06 || '',
    }),
    [currentValues]
  );

  const methods = useForm({
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    reset,
    watch,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  const isEssay = values.type === 'essay';

  useEffect(() => {
    reset(defaultValues);
  }, [reset, currentValues, defaultValues]);

  const onSubmit = handleSubmit(async (data: any) => {
    const form = {
      content: data.content,
      type: data?.type,
      level: +data.level,
      lines: +data.lines,
      point: +data.point,
      answer01: answers[0]?.content,
      answer02: answers[1]?.content,
      answer03: answers[2]?.content,
      answer04: answers[3]?.content,
      answer05: answers[4]?.content,
      answer06: answers[5]?.content,
      topic: {
        connect: { id: +data.topic },
      },
    };

    try {
      if (!isEdit) {
        const created = await createQuestion(form);

        onCreateNewRow({ ...created, name: created?.content });

        enqueueSnackbar('Đã thêm dữ liệu câu hỏi mới !');
      } else {
        const updated = await updateQuestion(form, currentValues.id);

        onUpdateRow({ ...updated, name: updated?.content });

        enqueueSnackbar('Đã cập nhật câu hỏi !');
      }
      reset();

      handleClose();
    } catch (error) {
      console.log(error);
      enqueueSnackbar('Đã có lỗi xảy ra ! Vui lòng thử lại !', { variant: 'error' });
    }
  });

  const handleChangeAnswer = ({ value, index }: { value: string; index: number }) => {
    const newAnswers = [...answers];

    newAnswers[index].content = value;

    setAnswers(newAnswers);
  };

  const handleNewAnswer = () => {
    if (answers.length >= 4) {
      enqueueSnackbar('Tối đa có 4 đáp án cho 1 câu hỏi !', { variant: 'error' });
    } else {
      setAnswers((pre: any) => [...pre, { content: '' }]);
    }
  };

  const handleDeleteAnswer = (index: number) => {
    const newAnswers = [...answers];

    newAnswers.splice(index, 1);

    setAnswers(newAnswers);
  };

  const handleClose = () => {
    onSetTableRowSelected(null);
    setAnswers([]);
    onClose();
  };

  const checkNumber = (value: any, name: string) => {
    if (Number(value) > 10 || Number.isNaN(Number(value))) {
      enqueueSnackbar('Dữ liệu không hợp lệ !', { variant: 'error' });
    } else {
      // @ts-ignore
      setValue(`${name}`, Number(value), { shouldValidate: true });
    }
  };

  return (
    <Dialog fullScreen open={open} onClose={handleClose}>
      <FormProvider methods={methods} onSubmit={onSubmit}>
        <DialogTitle>
          {isEdit ? 'Cập nhật dữ liệu câu hỏi' : 'Thêm dữ liệu câu hỏi mới'}
        </DialogTitle>

        <DialogContent>
          <Alert variant="outlined" severity="info" sx={{ mb: 3 }}>
            Ghi chú : Câu hỏi trắc nghiệm cần ít nhất 2 đáp án...
          </Alert>

          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <Stack spacing={2}>
                <BlockItem label="Chọn loại câu hỏi :" required>
                  <RHFSelect name="type" label="Chọn loại câu hỏi...">
                    {QUESTION_TYPE.map((option: any) => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </RHFSelect>
                </BlockItem>

                <BlockItem label="Độ khó câu hỏi :" required>
                  <RHFSlider
                    min={0}
                    max={5}
                    marks={TEST_DIFFICULT}
                    name="level"
                    valueLabelDisplay="on"
                    valueLabelFormat={(value: number) => `Cấp độ ${value}`}
                    sx={{ mt: 2.5, mx: 3 }}
                  />
                </BlockItem>

                <BlockItem label="Điểm của câu hỏi :" required>
                  <RHFTextField
                    name="point"
                    label="Nhập điểm câu hỏi..."
                    onChange={(event: any) => checkNumber(event.target.value, 'point')}
                  />
                </BlockItem>

                <BlockItem label="Câu hỏi thuộc môn học :" required>
                  <RHFSelect name="subject" label="Chọn môn học...">
                    {subjects?.map((subject: Subject) => (
                      <MenuItem key={subject.id} value={subject.id}>
                        {`${subject?.name} lớp ${subject?.grades
                          ?.map((grade) => grade?.label)
                          ?.join('')}`}
                      </MenuItem>
                    ))}
                  </RHFSelect>
                </BlockItem>

                <BlockItem label="Câu hỏi thuộc chủ đề :" required>
                  <RHFSelect name="topic" label="Chọn chủ đề..." disabled={!values.subject}>
                    {topics
                      ?.filter((topic: any) => topic?.subject?.id === values.subject)
                      ?.map((option: any) => (
                        <MenuItem key={option.id} value={option.id}>
                          {option?.name}
                        </MenuItem>
                      ))}
                  </RHFSelect>
                </BlockItem>

                {isEssay && (
                  <BlockItem label="Số hàng trả lời (tự luận) :" required>
                    <RHFTextField name="lines" label="Nhập số hàng..." />
                  </BlockItem>
                )}
              </Stack>
            </Grid>

            <Grid item xs={12} md={6}>
              <Stack spacing={2}>
                <BlockItem label="Câu hỏi :" required>
                  <RHFEditor simple name="content" sx={{ width: '100%' }} />
                </BlockItem>

                {!isEssay && (
                  <BlockItem label="Các đáp án :" required>
                    {answers?.map((answer: any, index: number) => (
                      <Grid
                        key={index}
                        container
                        spacing={1}
                        sx={{
                          typography: 'subtitle2',
                        }}
                      >
                        <Grid item xs={0.5}>
                          {QUESTION_ENUM_INDEX[index]}
                        </Grid>

                        <Grid item xs={10.5}>
                          <Editor
                            id={`editor-${index}`}
                            simple
                            value={answer.content}
                            onChange={(value: any) => handleChangeAnswer({ value, index })}
                          />
                        </Grid>

                        <Grid item xs={1}>
                          <IconButton color="error" onClick={() => handleDeleteAnswer(index)}>
                            <Iconify icon="mingcute:delete-back-fill" />
                          </IconButton>
                        </Grid>
                      </Grid>
                    ))}

                    <Stack paddingX={1}>
                      <Button variant="contained" color="primary" onClick={handleNewAnswer}>
                        Thêm đáp án mới
                      </Button>
                    </Stack>
                  </BlockItem>
                )}
              </Stack>
            </Grid>
          </Grid>
        </DialogContent>

        <DialogActions>
          <Button variant="outlined" onClick={handleClose}>
            Huỷ bỏ
          </Button>

          <LoadingButton type="submit" color="primary" variant="contained" loading={isSubmitting}>
            {isEdit ? 'Cập nhật dữ liệu' : 'Thêm dữ liệu mới'}
          </LoadingButton>
        </DialogActions>
      </FormProvider>
    </Dialog>
  );
}
