// react
import { useEffect } from 'react';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
// api
import useGetQuestion from 'src/api/controllers/question';
import useGetSubject from 'src/api/controllers/subject';
import useGetTopic from 'src/api/controllers/topic';
// route
import { paths } from 'src/routes/paths';
// utils
import { fDateTime } from 'src/utils/format-time';
// table
import TableData from '../../table/table-data';
// context
import { useTableContext } from '../../table/context';
import QuestionForm from './question-form';
import { TableHeader, TableRowCollapse, TableRows } from './question-table-item';

// ----------------------------------------------------------------------

export default function AllQuestionView() {
  const {
    _table_data,
    _table_open_form,
    onSetOpenForm,
    //
    onSetTableData,
    onSetTableHeader,
    onSetTableRows,
    onSetTableRowCollapse,
    onSetImportTableData,
    onSetExportTableData,
    onSetConfigTable,
  } = useTableContext();

  const { data: questions } = useGetQuestion();
  const { data: subjects } = useGetSubject();
  const { data: topics } = useGetTopic();

  useEffect(() => {
    onSetTableData(
      questions?.map((question: any) => ({
        ...question,
        name: question?.content,
      }))
    );

    onSetImportTableData({
      sample_data: [
        {
          'Tên câu hỏi': 'Trứng có trước hay gà có trước ?',
          'Loại câu hỏi': 'Trắc nghiệm',
          'Độ khó': 1,
          'Điểm câu hỏi': 0.25,
          'Đáp án số 1': 'Trứng có trước',
          'Đáp án số 2': 'Gà có trước',
          'Đáp án số 3': 'Khủng long có trước',
          'Đáp án số 4': 'Gà có trước',
          'Số hàng ( câu tự luận )': 5,
        },
      ],
      option_field: 'topicId',
      option_values: topics?.map((topic: any) => ({ value: topic._id, label: topic.name })),
    });

    onSetConfigTable({
      table_name: 'question',
      add_new: true,
      //
      filter_by_date: true,
      filter_by_type: false,
      filter_by_search: true,
      //
      import_data: true,
      export_data: true,
      //
      table_selected: true,
      allocation_student: false,
      delete_all: true,
      collapse_item: true,
      //
      edit_row: true,
      delete_row: true,
    });

    onSetTableHeader(TableHeader);
    onSetTableRows(TableRows);
    onSetTableRowCollapse(TableRowCollapse);
  }, [questions, topics]);

  useEffect(() => {
    onSetExportTableData(
      _table_data?.map((question: any) => ({
        'Câu hỏi': question?.content,
        'Loại câu hỏi': question?.type === 'choice' ? 'Câu hỏi trắc nghiệm' : 'Câu hỏi tự luận',
        'Đáp án A': question?.answer01,
        'Đáp án B': question?.answer02,
        'Đáp án C': question?.answer03,
        'Đáp án D': question?.answer04,
        'Đáp án E': question?.answer05,
        'Đáp án F': question?.answer06,
        'Điểm câu hỏi': `${question?.point} điểm`,
        'Độ khó': `Cấp độ ${question?.level}`,
        'Ngày tạo': fDateTime(question?.createdAt),
        'Ngày cập nhật': fDateTime(question?.updatedAt),
      }))
    );
  }, [_table_data]);
  return (
    <>
      <CustomBreadcrumbs
        heading="Quản lý câu hỏi"
        links={[
          { name: 'Danh mục' },
          {
            name: 'Bài thi',
            href: paths.home.test.root,
          },
          { name: 'Câu hỏi' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <TableData />

      <QuestionForm
        open={_table_open_form}
        onClose={() => onSetOpenForm(false)}
        topics={topics}
        subjects={subjects}
      />
    </>
  );
}
